@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
body {
  background-color: rgb(61, 59, 59);
  margin: 0;
  font-family: 'Otomanopee One', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(61, 59, 59);
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

@media only screen and (max-width: 600px){
  ::-webkit-scrollbar {
    display: none;
  }
}
.footer{
    background-color: rgb(11, 11, 27);
    left: 0;
    bottom: 0;
    color: aliceblue;
    text-align: center;
    padding: 2%;
}
.bgeffect{
    background-color: rgb(36, 35, 35);
    margin-top: 5%;
    margin-left: 10%;
    height: auto;
    width: 80%;
    border-radius: 5% 15%;
    line-height: 80%;
}


.bgeffect {
	position: relative;
	/* background: linear-gradient(0deg, #000, #272727); */
}

.bgeffect:before, .bgeffect:after {
    border-radius: 5% 15%;
	content: '';
	position: absolute;
	left: -2px;
	top: -2px;
	background: linear-gradient(45deg, #fb0094, #0000ff, #00ff00,#ffff00, #ff0000, #fb0094, 
		#0000ff, #00ff00,#ffff00, #ff0000);
	background-size: 400%;
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	z-index: -1;
	-webkit-animation: steam 70s linear infinite;
	        animation: steam 70s linear infinite;
}

@-webkit-keyframes steam {
	0% {
		background-position: 0 0;
	}
	50% {
		background-position: 400% 0;
	}
	100% {
		background-position: 0 0;
	}
}

@keyframes steam {
	0% {
		background-position: 0 0;
	}
	50% {
		background-position: 400% 0;
	}
	100% {
		background-position: 0 0;
	}
}

.bgeffect:after {
	-webkit-filter: blur(20px);
	        filter: blur(20px);
}


.card{
    background-color: rgb(11, 11, 27);
    /* margin-top: 5%;
    margin-left: 10%; */
    color:aliceblue;
    /* height: auto;
    width: 100%; */
    /* position: fixed; */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20% 30%;
}

ul{
    line-height: 120%;
}

h4{
    line-height: 130%;
}

.card img{
    margin: 8%;
    margin-right: 10%;
    height: auto;
    width: 30%;
    border-radius: 50%;
}

.info{
    padding: 5%;
    margin-left: 5%;
    text-align: center; 
    vertical-align: middle;
}

@media only screen and (max-width: 600px){
    .bgeffect{
        margin-top: 15%;
        margin-bottom: 5%;
    }
    .card{
        display: block;
        margin-bottom: auto;
    }
    .card img{
        padding-top: 5%;
        height: auto;
        width: 80%;
        border-radius: 50%;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .info{
        padding-bottom: 20%;
        margin-left: 0%;
    }
}
.nav{
    z-index: 2;
    background-color: black;
    color: aliceblue;
    position: fixed;
    height: 7%;
    width: 100%;
    margin-top: 0%;
    padding-top: 0%;
    top: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
}

a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}

.nav a{
    margin: 8%;
    border-bottom: 2px solid white;
}

.nav .logoname{
    left: 2%;
    position: fixed;
    border: 2px solid white;
    border-radius: 40%;
    padding: 5px;
}

.nav a:hover{
    border-bottom: 2px solid cyan;
    color: cyan;
    display: block;
}

@media only screen and (max-width: 600px){
    .nav .logoname{
        display: none;
    }
}
